@import url("https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@400;500;700&display=swap");

.img {
  height: 20px;
  width: 20px;
}

.button {
  border: none;
  background-color: white;
}

.column {
  display: flex;
  justify-content: space-between;
}
.imageColumn {
  width: 3.6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.column h3 {
  font-family: "Barlow Condensed", sans-serif;
  font-weight: 500;
  font-size: 20px;
}
.li {
  font-family: "Barlow Condensed", sans-serif;
  font-weight: 500;
  font-size: 20px;
}
