@import url("https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@400;500;700&display=swap");
.box {
  margin: 1.8rem 0;
  padding: 0.8rem;
  background-color: rgb(214, 226, 230);
  height: 19rem;
  display: flex;

  flex-direction: column;

  justify-content: center;
}
.h1 {
  text-align: center;
  font-family: "Barlow Condensed", sans-serif;
  font-weight: 700;
  font-size: 30px;
}
.p {
  font-family: "Barlow Condensed", sans-serif;
  font-weight: 500;
  font-size: 16px;
}
.list {
  padding-left: 1rem;
}
.box2 {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.list li {
  font-family: "Barlow Condensed", sans-serif;
  font-weight: 400;
  font-size: 16px;
}
